<template>
	<div id="bird-egg">
		<div class="header">
			<div class="header-title">
				<img class="lt-arrow" src="../../../assets/arrow.png" @click="goBack"/>
				<span class="title">我的鸟蛋</span>
				<span class="header-right" @click="linkToUrl({name:'ArticleDetails',query:{id:id}})">规则说明</span>
			</div>
			<div class="header-info">
				<div class="info-left">
					<div class="info-title">
						<em>{{eggTotal}}</em>鸟蛋
					</div>
					<div class="info-text">鸟蛋可用于兑换虚拟礼物</div>
				<div class="info-right" @click="getEgg">
					<img class="egg-icon" src="../../../assets/my_btn_bird_egg_white@2x.png"/>
					<span>{{eggNum}}</span>
				</div>
				</div>
			</div>
		</div>
		<div class="body">
			<div class="body-title">
				鸟蛋明细
			</div>
			<div class="body-list">
				<!--<div class="list-item">
					<div class="list-title">被偷鸟蛋</div>
					<div class="list-time">2017-12-11   12:32:00</div>
					<span class="list-num">-5.0</span>
				</div>-->
				
				<div class="list-item" v-for="(item,index) in accountLogs">
					<div class="list-title">{{item.note}}</div>
					<div class="list-time">{{item.transTime}}</div>
					<span class="list-num" :class="{active : item.amount > 0}">{{item.amount > 0 ? `+${item.amount}` : item.amount}}</span>
				</div>
				
			</div>
		</div>
		<div class="footer">
			<div class="footer-num">
				<span>数量</span>
				<input type="number" v-model="num"/>
			</div>
			<div class="footer-btn" @click="exchageEgg()">兑换零钱</div>
		</div>
	</div>
</template>

<script>

const BIRDEGG = 12

export default {
	data(){
		return {
			eggTotal:0,
			eggNum:0,
			num:0,
			accountLogs:[],
			id:'',
		}
	},
  components: {
  	
  },
  created(){
  	this.getMyEggInfo()
  	this.getAccountDetailList()
  	this.getQuerySetting()
  },
  methods:{
  	getEgg(){
  		let user = JSON.parse( localStorage.getItem('UserDetails') )
  		let id = user.id
  		this.$Api.GetEggSubmit(id).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  					this.$toast('掏取成功')
  					this.getMyEggInfo()
  					this.getAccountDetailList()
  			}else{
  				this.$toast(res.q.d)
  			}
  		})
  	},
  	exchageEgg(){
  		if(this.num > 0 && this.num <= this.eggTotal){
  			this.$popup({
  				content:`确认兑换${this.num}个鸟蛋?`
  			}).then((res)=>{
  				if(res == 'sure'){
  					this.getExchangeEggSubmit()
  				}
  			})
  		}else{
  			this.$toast('请输入正确的兑换数目!')
  		}
  	},
  	getMyEggInfo(){
  		this.$Api.getMyEggInfo(1).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.eggTotal = res.q.eggTotal
  				this.eggNum = res.q.eggNum
  			}
  		})
  	},
  	getAccountDetailList(){
  		this.$Api.getAccountDetailList(BIRDEGG).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.accountLogs = res.q.accountLogs
  			}
  		})
  	},
  	getExchangeEggSubmit(){
  		if(this.num >= 500){
  			this.$Api.getExchangeEggSubmit(1,this.num).then((res)=>{
  				console.log(res)
  				if(res.q.s == 0){
  					this.$toast('兑换成功')
  					this.getMyEggInfo()
  					this.getAccountDetailList()
  				}else{
  					this.$toast(res.q.d)
  				}
  			})
  		}else{
  			this.$toast('鸟蛋兑换数量必须大于等于500')
  		}
  	},
  	getQuerySetting(){
  		let key = '鸟蛋规则说明'
  		this.$Api.getQuerySetting(key).then((res)=>{
  			console.log(res)
  			if(res.q.s==0){
  				this.id = res.q.settings.value
  			}
  		})
  	}
  },
  
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#bird-egg{
		height: 100%;
		background: #f0f0f0;
		.header{
			width: 100%;
			height: 3.73rem;
		  color: #fff;
			@include bg-color;
			.header-title{
		    width: 100%;
		    height: 1.17rem;
		    line-height: 1.17rem;
		    padding: 0 0.4rem;
		    text-align:center;
				position: relative;
				.lt-arrow{
					display: inline-block;
					@include lt-arrow;
				}
				.title{
					font-size: 0.48rem;
				}
				.header-right{
					position: absolute;
					font-size: 0.4rem;
					right: 0.4rem;
				}
			}
			.header-info{
				padding:0.4rem 0.4rem 0 0.51rem; 
				.info-left{
					position: relative;
					line-height: 1;
					.info-title{
						font-size: 0.43rem;
						>em{
							font-size: 0.77rem;
							margin-right: 0.13rem;
						}
					}
					.info-text{
						font-size: 0.35rem;
						padding-top: 0.24rem;
					}
				}
				.info-right{
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: 0.27rem;
					.egg-icon{
						width: 0.56rem;
						height: 0.75rem;
					}
				}
			}

		}
		.body{
			.body-title{
				font-size: 0.43rem;
				height: 1.2rem;
				line-height: 1.2rem;
				padding: 0 0.4rem;
				color: #1a1a1a;
			}
			.body-list{
				position: absolute;
				top: 3.73+1.2rem;
				bottom: 1.31rem;
				width: 100%;
				background: #fff;
				overflow-y: auto;
				.list-item{
					position: relative;
					line-height: 1;
					padding: 0.4rem;
					border-bottom: 1px solid $border-color;
				}
				.list-title{
					font-size: 0.4rem;
					color: #333;
				}
				.list-time{
					font-size: 0.37rem;
					padding-top: 0.24rem;
					color: #666;
				}
				.list-num{
					position: absolute;
					right: 0.4rem;
					top: 50%;
					transform: translateY(-50%);
					font-size: 0.53rem;
					color: #333;
				}
				.active{
					color: #de7fef;
				}
			}
		}
		.footer{
			position: fixed;
			bottom: 0;
			width: 100%;
			height: 1.31rem;
			line-height: 1.31rem;
			border-top: 1px solid $border-color;
			background: #fff;
			.footer-num{
				float: left;
				padding-left: 0.4rem;
				font-size: 0.37rem;
				color: #1a1a1a;
				>span{
					margin-right: 0.67rem;
				}
				input{
					font-size: 0.48rem;
					color: #333;
					width: 1.89rem;
  				height: 0.83rem;
  				line-height: 0.83rem;
  				text-align: center;
  				border: 1px solid $border-color;
				}
			}
			.footer-btn{
				float: right;
				font-size: 0.45rem;
				width: 4.36rem;
				height: 1.31rem;
				line-height: 1.31rem;
				color: #fff;
				text-align: center;
				@include bg-color;
			}
		}
		input{
			-webkit-appearance:none; 
			resize: none;
			outline: none;
			border-radius: 0;
		}
	}
</style>


